@use '_partials/_variables' as v;
@use '_partials/_mixins' as m;

/* VII. Media-queries
--------------------------------------- */

@media (max-width: 1699.98px){
	.about{
		&__box{
			max-width: 50rem;
		}
	}

	.goods{
		&__box{
			max-width: 50rem;
		}
	}

	.overviews{
		&__otv{
			right: -21rem;
		}
	}
}
// XL
@media (max-width: 1399.98px){
	html{
		font-size: 9px;
	}
	.container{
		max-width: 114rem;
	}
	.d{
		&-block{
			&_xl{
				display: block;
			}
		}
		&-none{
			&_xl{
				display: none;
			}
		}
	}
	.intro{
		&__content{
			max-width: 81rem;
		}
		&__subheading{
			display: block;
		}
		&__man{
			right: -3rem;
		}

	}
	.form{
		&-section{
			&__form{
				justify-content: space-around;
			}
		}
		&__group{
			max-width: 305px;
		}
		&__submit{
			// width: 280px;
		}
	}

	.good{
		&__img{
			left: -43rem;
			img{
				max-width: 43rem;
			}
		}
	}

	.prize{
		&__sims{
			bottom: 25rem;
		}
	}

	.buy{
		&__box{
		left: -16rem;
		bottom: -38rem;
		}
	}

	.overviews {
		&__yt{
			left: -15rem;
			max-width: 33.6rem;	
		}
		&__cam{
			right: -19rem;
			max-width: 40rem;
		}
		&__otv{
			top: 44rem;
			right: -16rem;
			max-width: 38rem;
		}
	}

	.social{
		&__phones{
			img{
				max-width: 45rem;
			}
		}
	}

	.questions{
		&__img2{
			right: 1.5rem;
		}
	}

	.partn{
		&__bg{
			img{
				object-position: -15rem center;
			}
		}
	}
}




// LG
@media (max-width: 1199.98px){
	html{
		// font-size: 8px;
	}
	h1{
		font-size: 3.2rem;
	}
	.container{
		&,
		&_xl{
			max-width: 96rem;
		}
	}
	.d{
		&-block{
			&_lg{
				display: block;
			}
		}
		&-none{
			&_lg{
				display: none;
			}
		}
	}
	.header{
		&__logo{
			margin-right: 0;
		}
		&__work{
			display: none;
		}
	}
	.top-menu{
		&-wrapper{
			max-width: 42rem;
		}
	}
	.intro{
		&__content{
			max-width: 65rem;
		}
		&-power{
			&__img{
				&_arrow{
					display: none;
				}
			}
		}
	}

	.about{
		&-grid{
			grid-template-columns: 1fr 1fr 1fr;
		}
		&__box{
			max-width: 40rem;
			bottom: -31rem;
		}
	}

	.good{
		padding: 3rem 3rem 3rem 30.6rem;
		&__header{
			font-size: 2rem;
			font-weight: 600;
		}
		&__name{
			font-size: 2rem;
		}
		&__img{
			left: -40rem;
			img{
				max-width: 40rem;
			}
		}
	}

	.important{
		&__content{
			.center-img{
				&__street{
					top: calc(50% + 8.5rem);
				}
				&__round{
					top: 51%;
				}
				img{
					max-width: 45rem;
				}
			}
		}
	}

	.prize{
		&__sims{
			max-width: 35rem;
			right: -13rem;
		}
	}

	.buy{
		&__grid{
			display: block;
		}
		&-img{
			margin-top: 6rem;
			margin-bottom: 6rem;
			padding-bottom: 0;
			.img-grid{
				grid-template-columns: 1fr 1fr 1fr 1fr;
				grid-template-rows: auto;
			}
		}
	}

	.overviews{
		&__yt{
			left: -10rem;
			max-width: 30rem;
		}
		&__cam{
			right: -15rem;
			max-width: 35rem;
		}
		&__otv{
			top: 37rem;
			right: -10rem;
			max-width: 30rem;
		}
	}

	.main-video{
		&__img {
			img{
				max-width: 65rem;
				height: auto;
			}
		}
	}

	.questions{
		&__img2{
			top: -5rem;
		}
	}

	.footer{
		&__grid{
			grid-template-columns: 1fr 1fr 1fr;
			grid-template-rows: 1fr 1fr;
			// row-gap: 4rem;
			grid-template-areas:	"f-logo		contacts	contacts"
									"f-menu-1	f-menu-2	f-menu-3";
		}
		&__logo{
			grid-area: f-logo;
		}
		&-menu{
			&-1{
				grid-area: f-menu-1;
			}
			&-2{
				grid-area: f-menu-2;
			}
			&-3{
				grid-area: f-menu-3;
			}
		}
		&-contacts{
			grid-area: contacts;
			padding-left: 0;
			&__body{
				display: grid;
				grid-template-columns: 1fr 1fr;
				grid-template-areas:	"c-phone	c-loc"
										"c-mail		c-loc"
										"c-inn		c-loc"
										"c-ogrn		c-loc";
			}
			&__phone{
				grid-area: c-phone;
			}
			&__mail{
				grid-area: c-mail;
			}
			&__location{
				max-width: 25rem;
				line-height: 3.5rem;
				grid-area: c-loc;
			}
			&__ogrn{
				grid-area: c-ogrn;
			}
			&__inn{
				grid-area: c-inn;
			}
		}
		&-menu{
			&__item{
				max-width: none;
			}
		}
	}
}

// MD
@media (max-width: 991.98px){
	html{
		font-size: 8px;
	}
	h1{
		line-height: 1.4;
	}
	h2{
		font-size: 3.25rem;
	}
	h3{
		font-size: 3rem;
	}
	.btn{
		&_primary{
			padding-left: 2em;
			padding-right: 2em;
		}
		&_secondary{
			padding: 0 1.9em;
			font-size: 15px;
		}
	}
	.container{
		&,
		&_xl,
		&_lg{
			max-width: 740px;
		}
	}

	//  
	.d{
		&-desktop{
			display: none;
		}
		&-mobile{
			display: block;
		}
	}
	//   

	.d{
		&-block{
			&_md{
				display: block;
			}
		}
		&-inline{
			&_md{
				display: inline;
			}
		}
		&-none{
			&_md{
				display: none;
			}
		}
		// &-none{
		// 	&_md{
		// 		display: none;
		// 	}
		// }
	}

	.header{
		&__container{
			position: relative;
			// padding-right: 0;
			justify-content: flex-end;
			// max-width: none;

			// padding-top: 0;
			// padding-bottom: 0;
		}
		&__work{
			display: block;
			margin-left: 1.5rem;
			max-width: 12rem;
			font-size: 16px;
		}
		&-nav{
			position: absolute;
			z-index: 990;
			// padding: 5rem 0 5rem 3rem;
			width: 100%;
			background: #fff;
			overflow: hidden;
			// height: 0;
			border-top: solid 1px #E8EAF4;

			border-radius: 0px 0px 0px 1.6rem;
			box-shadow: 0px 25px 40px rgba(146, 171, 234, 0.3);
			opacity: 0;
			visibility: hidden;
			transform: translateX(50px);
			transition: all .3s;
			&.open{
				transform: translateX(0px);
				// height: auto;
				opacity: 1;
				visibility: visible;
			}
		}
		&-contacts{
			margin-left: 0;
			padding-left: 3rem;
			// padding-top: 2.5rem;
			// padding-bottom: 2.5rem;
			// box-shadow: 0px 25px 40px rgba(146, 171, 234, 0.3);
		}
		&-phone{
			a{
				font-size: 20px;
			}
		}
		&-mail{
			a{
				font-size: 18px;
			}
		}
	}

	.logo{
		&-wrapper{
			margin-right: auto;
		}
	}

	.top-menu{
		margin: 6rem 0 5rem 3rem;
		display: block;
		max-width: 34rem;
		&-wrapper{
			display: block;
			position: absolute;
			bottom: 0;
			left: 58rem;
			width: 100%;
			max-width: 52rem;
			min-width: auto;
			height: 0;
		}
		&__item{
			font-size: 18px;
			&:not(.top-menu__item_last){
				margin-bottom: 5.3rem;
			}
		}
		&-toggler{
			margin-left: 2.5rem;
			position: relative;
			display: block;
			width: 40px;
			height: 40px;
			border: solid 1px v.$top-menu-toggler-border;
			font-size: 10px;
			border-radius: .6em;
			&__item{
				position: absolute;
				left: .9em;
				width: 2em;
				height: 0.2em;
				border-radius: 1px;
				background: v.$top-menu-toggler-border;
				transition: transform .3s;
				&:first-child{
					top: calc(50% - .1em);
				}
				&:nth-child(2){
					top: calc(50% - .7em);
				}
				&:last-child{
					top: calc(50% + .5em);
				}

			}
			&.open{
				.top-menu-toggler__item{
					&:first-child{
						opacity: 0;
					}
					// &:nth-child(2),
					// &:last-child{
					// 	transform-origin: 0;
					// }
					&:nth-child(2){
						transform: translateY(.6em) rotate(45deg);
					}
					&:last-child{
						transform: translateY(-.6em) rotate(-45deg);
					}
				}
			}
		}
		&-overl{
			display: block;
			position: fixed;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 980;
			background: rgba(0, 38, 255, 0);
			opacity: 0;
			visibility: hidden;
			transition: all .3s;
			&.open{
				opacity: 1;
				visibility: visible;
			}
		}
	}

	.intro{
		&__container{
			padding-top: 8.1rem;
			padding-bottom: 11rem;
		}
		&__content{
			max-width: 440px;
			// z-index: 5;
		}
		// &__subheading{
		// 	display: block;
		// }
		&-power{
			position: relative;
			margin-top: 5rem;
			max-width: 52rem;
			&__inner{
				padding-left: 2rem;
			}
			&__img{
				&_street{
					display: none;
				}
			}
		}
		&-offer{
			margin-top: 6rem;
		}
		&__man{
			right: -6rem;
			bottom: 4rem;
		}
		// &__clouds{

		// 	top: auto;
		// 	height: 0;
		// 	&>img{
		// 		position: absolute;
		// 		bottom: 0;
		// 		max-width: 100%;
		// 		height: auto;
		// 	}
		// }
	}

	.form-section{
		&__inner{
			padding-left: 5rem;
			padding-right: 5rem;
		}
		&__heading,
		&__text{
			margin-left: auto;
			margin-right: auto;
			max-width: 65rem;
		}
		&__heading{
			h3{
				font-size: 3rem;	
			}
		}
		&__subheading{
			font-size: 3rem;
		}
		&__text{
			font-size: 2.25rem;
		}
		&__submit{
			max-width: 305px;
    		width: 100%;
		}
	}

	.quote{
		padding: 4rem 10rem;
		&__text{
			.text_bold{
				max-width: 50rem;
				display: inline-block;
			}
		}
	}

	.want-check {
		padding-top: 3rem;
		&__phone{
			max-width: 19.2rem;
			right: -1rem;
			bottom: -12rem;
			z-index: 5;
		}
		&__modem {
			max-width: 21.5rem;
			left: 1rem;
			top: -7rem;
			z-index: 8;
		}
	}

	.stars{
		&__subheading{
			font-size: 2.25rem;
		}
		&__slider{
			position: relative;
			margin: 0 auto;
			max-width: 720px;
		}
		&-slider{
			&__bg{
				img{
					object-position: 86% bottom;
				}
			}
		}
		&-slide{
			// width: 90rem;
			height: 45.5rem;
			opacity: 1;
			transition: none;
		}
		&-card{
			margin-left: 4rem;
			width: 51.25rem;
			height: 31rem;
			&__heading{
				h4{
					font-size: 2.5rem;
				}
				&__text{
					font-size: 2rem;
				}
			}
		}
		.swiper-button{
			&-prev{
				left: 0;
			}
			&-next{
				right: 0;
			}
		}
	}

	.about{
		&-grid{
			&-wrapper{
				padding: 0;
			}
		}
		&__box{
			display: none;
		}
		&__bg{
			img{
				width: 90rem;
				height: auto;
				object-fit: fill;
				object-position: 50% -15rem;
			}
		}
	}

	.goods{
		&__heading{
			margin-bottom: 12.5rem;
		}
	}

	.good{
		padding: 0 5rem;
		&:not(:last-child){
			margin-bottom: 15rem;
		}
		&__content{
			top: -7.5rem;
		}
		&__header{
			margin-top: 5rem;
		}
		&__img{
			&-wrapper{
				position: relative;
			}
			&-bg{
				display: block;
				position: relative;
				// top: 50%;
				// transform: translateY(-50%);
				// left: 0;
				// right: 0;
				// width: 100%;
				// height: 15rem;
				// text-align: center;
				// &_street-2{
	
				// }
				// &_street-2-pro{
					
				// }
				// &_street-ultra-pro{
					
				// }
				img{
					margin: 0 auto;
					display: block;
					width: 100%;
					max-width: 80rem;
				}
			}
			// position: relative;
			transform: translate(-50%, -50%);
			top: 50%;
			right: auto;
			bottom: auto;
			left: 50%;
			img{
				display: block;
				margin-left: auto;
				margin-right: auto;
				max-width: 30rem;
			}
		}
		&__name{
			font-size: 2.5rem;
		}
		&__adv{
			font-size: 2rem;
		}
		&__prices{
			.price{
				font-size: 2rem;
				&-name {
					font-size: 1.75rem;
				}
				&_part{
					.dig{
						font-size: 3.25rem;
					}
					.rub{
						font-size: 3rem;
					}
				}
				&_total{
					.dig{
						font-size: 2.5rem;
					}
				}
			}
		}
		&-btn{
			&_primary,
			&_outline{
				width: 100%;
				max-width: 35.25rem;
				height: 7.5rem;
				font-size: 2rem;
			}
			&_primary{
				margin-right: 3.25rem;
			}
		}
	}

	.help{
		margin-bottom: 7.5em;
		z-index: 10;
		&__text{
			max-width: 60rem;
		}
	}

	.important{
		padding-bottom: 5rem;
		z-index: 2;
		&__container{
			z-index: 10;
		}
		&-heading{
			h2{
				margin-left: auto;
				margin-right: auto;
				max-width: 26rem;
			}
		}
		&__row{
			&:nth-child(2){
				margin-top: 8rem;
			}
			&_center{
				margin-top: -9rem;
			}
		}
		&__bg{
			z-index: 5;
			img{
				// object-fit: contain;
				object-position: left bottom;
			}
		}
		&__tree{
			z-index: 6;
			&_l{
				top: -41rem;
				left: 0;
			}
			&_r{
				top: 0;
				right: 0;
			}
		}
		&__item{
			font-size: 1.75rem;
			.item{
				&__title{
					margin-top: 2rem;
					font-size: 2rem;
				}
			}
			&_last{
				max-width: 45rem;
			}
		}
	}

	.prize{
		padding-top: 12rem;
		&__heading{
			max-width: 82rem;
		}
		&__text{
			max-width: 62rem;
		}
		&__sims{
			max-width: 28.5rem;
			right: 0rem;
			top: 28rem;
		}
	}

	.buy{
		padding-top: 15rem;
		padding-bottom: 2rem;
		&__heading{
			margin-bottom: 5rem;
		}
		&__grid{
			display: block;
		}
		&-text{
			&__heading{
				h4{
					font-size: 2.75rem;
				}
			}
			&__body{
				font-size: 2.25rem;
			}
		}
		&__box{
			right: -6rem;
			bottom: -40rem;
			left: unset;
		}
		&-img{
			.img-grid{
				row-gap: 4rem;
			}
		}
	}

	.reviews{
		&__heading{
			margin-bottom: 5rem;
		}
		&-slide{
			width: 37.75rem;
		}
		.swiper-button-prev,
		.swiper-button-next{
			top: 59rem;
		}
		.swiper-button-prev{
			left: calc(68% - 37.5rem - 10rem);
		}
		.swiper-button-next{
			right: calc(68% - 37.5rem - 10rem);
		}
	}

	.overviews{
		padding-top: 1.5rem;
		padding-bottom: 12.5rem;
		&__otv{
			display: none;
		}
		&__yt{
			top: -24rem;
			left: -2rem;
		}
		&__cam{
			display: none;
		}
		&__video{
			&-container{
				margin-top: 5rem;
			}
		}
	}

	.main-video{
		&__img{
			img{
				margin-bottom: 5rem;
				max-width: 100%;
			}
		}
	}

	.video{
		&-grid{
			column-gap: 3.375rem;
		}
		&__img{
			border-width: .75rem;
		}
	}

	.social{
		padding: 7.5rem 0;
		height: auto;
		&__content{
			flex-direction: column;
			align-items: flex-start;
		}
		&__heading{
			font-size: 3.25rem;
		}
		&__links{
			margin-top: 2.5rem;
			.yt{
				margin-left: 0;
			}
		}
		&__phones{
			top: -37rem;
			transform: none;
			&-wrapper{
				margin-left: 34rem;
			}
			img{
				max-width: 59rem;
			}
		}
	}

	.questions {
		&__img1{
		left: 3rem;
		bottom: -30rem;
		top: auto;
		}
		&__img2{
			top: -4rem;
		}
		&__text{
			max-width: 57rem;
		}
	}

	.partn{
		padding-top: 15.5rem;
		&__container{
			max-width: none;
			padding-left: 0;
			padding-right: 0;
		}
		&__heading{
			// margin-bottom: 3.75;
		}
		&__content{
			margin-top: 0;
			padding-bottom: 57rem;
			position: relative;
			overflow: hidden;
			&_left,
			&_right{
				padding-left: 1.5rem;
				padding-right: 1.5rem;
				
			}
			&_left{
				// margin-left: auto;
				// margin-right: auto;
				// max-width: 740px;
				position: absolute;
				bottom: 5rem;
				max-width: none;
				// top: -(v.$content-right-offset-y);


			}
			&_right{
				position: relative;
			}
			flex-direction: column-reverse;
		}
